import { injectHTML, injectHTMLAndRunScript } from '@/utils/utils';
import { CheckoutParams, IConversion, PurchaseParams, SearchParams } from './types';

export class FacebookConversion implements IConversion {
  public static initialized = false;

  constructor(private conversionId: string) {}

  public init() {
    if (FacebookConversion.initialized) return;

    const script = `
      <script>
      !function(f,b,e,v,n,t,s)
      {if(f.fbq)return;n=f.fbq=function(){n.callMethod?
      n.callMethod.apply(n,arguments):n.queue.push(arguments)};
      if(!f._fbq)f._fbq=n;n.push=n;n.loaded=!0;n.version='2.0';
      n.queue=[];t=b.createElement(e);t.async=!0;
      t.src=v;s=b.getElementsByTagName(e)[0];
      s.parentNode.insertBefore(t,s)}(window,document,'script',
      'https://connect.facebook.net/en_US/fbevents.js');
      fbq('init', '${this.conversionId}');
      fbq('track', 'PageView');
      </script>
      <noscript>
      <img height="1" width="1" src="https://www.facebook.com/tr?id=${this.conversionId}&ev=PageView&noscript=1" />
      </noscript>
      `;
    injectHTML(script, 'head', 'first');

    FacebookConversion.initialized = true;
  }

  public trackCheckout({ currency, value }: CheckoutParams): void {
    const script = `
        fbq('track', 'InitiateCheckout', {
          value: ${value},
          currency: ${currency}
         });
        `;
    injectHTMLAndRunScript(script, 'checkout');
  }
  public trackSearch({ from_date, to_date }: SearchParams) {
    const script = `
        fbq('track', 'Search', {
          search_string: '${from_date} / ${to_date}'
        });
    `;
    injectHTMLAndRunScript(script, 'search');
  }
  public trackPurchase({ currency, value }: PurchaseParams): void {
    const script = `
      fbq('track', 'Purchase', {
        value: ${value},
        currency: ${currency}
      });
  `;
    injectHTMLAndRunScript(script, 'purchase');
  }
}
